.otp-container {
  display: flex;
  width: 100%;
  max-width: 360px;
  column-gap: 10px;
}

.otp-input {
  width: 100%;
  height: 60px;
  border: 1px solid #ccc;
  border-radius: 5px;
  text-align: center;
  font-size: 32px;
  font-weight: 700;
  line-height: 1;
}
.otp-input:disabled {
  cursor: not-allowed;
}
.go-back-button {
  margin-top: 15px;
}

.error {
  color: red;
}
